//import TUIImageEditor from '@toast-ui/react-image-editor';


const ImageEditor = () => {

  const isBrowser = typeof window !== "undefined"

  React.useEffect(() => {

    if(isBrowser){

      // Create image editor
      //@ts-ignore
      let imageEditor = new tui.ImageEditor('#my-image-editor canvas', {
        includeUI: {
          loadImage: {
            path: '/images/ReInHerit-1.jpg',
            name: 'SampleImage',
          },
          initMenu: 'filter',
          menuBarPosition: 'bottom',
        },
        cssMaxWidth: 700,
        cssMaxHeight: 500,
        selectionStyle: {
          cornerSize: 20,
          rotatingPointOffset: 70,
        },
      });

      // Load image
      // imageEditor.loadImageFromURL('https://picsum.photos/200/300', "My image");

    }

  }, [])

  return <>
  <link type="text/css" href="https://uicdn.toast.com/tui-color-picker/v2.2.6/tui-color-picker.css" rel="stylesheet"/>
<link rel="stylesheet" href="https://uicdn.toast.com/tui-image-editor/v3.15.0/tui-image-editor.css"/>

<script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/fabric.js/3.6.0/fabric.js"></script>
<script type="text/javascript" src="https://uicdn.toast.com/tui.code-snippet/v1.5.0/tui-code-snippet.min.js"></script>
<script type="text/javascript" src="https://cdnjs.cloudflare.com/ajax/libs/FileSaver.js/1.3.3/FileSaver.min.js"></script>
<script type="text/javascript" src="https://uicdn.toast.com/tui-color-picker/v2.2.6/tui-color-picker.js"></script>
<script src="https://uicdn.toast.com/tui-image-editor/v3.10.0/tui-image-editor.js"></script>

<div id="my-image-editor" style={{height: "800px"}}>
  <canvas></canvas>
</div>
</>
}


export default ImageEditor;